html {
  box-sizing: border-box;
  font-size: 16px;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: 'Montserrat', sans-serif;
}


.navbar {
  width: 100%;
  height: 3em;
  position: fixed;
  background: rgba(208, 208, 208, 0.7);
  //background: rgba(255,255,255,0.7);
  display: flex;
  align-items: center;
  z-index: 100;

}

.navbar-ul {
  width: 100%;
  height: 100%;
  display: flex;
  list-style: none;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  text-transform: uppercase;
}

.navbar-div-home {
  min-width: 20%;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 1em;

  li {
    height: 100%;
    display: flex;
    align-items: center;
  }

  a {
    text-decoration: none;
    color: black;
    padding: 1em;
    height: 100%;
    display: flex;
    align-items: center;

    &:hover {
      transition-duration: 0.25s;
      background-color: rgba(74, 198, 178, 0.7);
    }
  }
}

.navbar-div-nav {
  display: flex;
  align-items: center;
  height: 100%;

  li {
    height: 100%;
    display: flex;
    align-items: center;
  }

  a {
    text-decoration: none;
    color: black;
    white-space: nowrap;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 1em;
  }

  .about-me-nav,
  .projects-nav,
  .contact-nav {
    &:hover {
      transition-duration: 0.5s;
      background-color: rgba(74, 198, 178, 0.7);
    }
  }
}

.flag_a {
  width: 80px;
  cursor: pointer;

  img {
    width: 75%;
  }
}

.welcome-header-name {
  width: 60%;
  height: 50%;
  font-size: 5.5em;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  white-space: nowrap;
  padding: 10px;
  animation: welcome-header-name 2s forwards;
  opacity: 0
}

@keyframes welcome-header-name {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.main-page-line {
  width: 30%;
  height: 5px;
  background: black;
  position: absolute;
  bottom: 1.7em;
  left: 1.5em;
  border-radius: 0% 100% 0% 100% / 72% 100% 0% 28%;
}

.main-page-text {
  width: 4em;
  height: 0.7em;
  position: absolute;
  left: 5.7em;
  bottom: 2em;
  display: flex;
  justify-content: center;
  font-size: 43px;

}


.line-color {
  width: 100%;
  height: 3px;
  background-color: black;
}

.scroll {
  width: 20px;
  height: 40px;
  border: 1px solid #4AC6B2;
  border-radius: 10px;
  position: absolute;
  bottom: 2%;
  left: 50%;

  &:before {
    content: "";
    display: block;
    width: 14px;
    height: 14px;
    background: #4AC6B2;
    margin: 2px auto;
    border-radius: 50%;
    animation: scroll 1s 0s infinite alternate ease-in-out;
  }
}

@keyframes scroll {
  0% {
    margin-top: 2px;
    transform: scale(1, 0.8);
  }
  50% {
    transform: scale(0.8, 1);
  }
  100% {
    margin-top: 23px;
    transform: scale(1, 0.8);
  }
}

.about-title {
  width: 100%;
  height: 12%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.about_me_span {
  font-size: 4.5em;
  text-transform: uppercase;
}

.about_me_line {
  width: 15%;
  height: 4px;
  background: black;
  border-radius: 0% 100% 0% 100% / 72% 100% 0% 28%;
}

.about_me_desc {
  width: 100%;
  height: 50%;
  display: flex;
}

.about_me_photo {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about_me_text {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

}

.me_description {
  width: 80%;
  height: 55%;
  margin-left: -12em;
  position: relative;

  &::after {
    content: "";
    width: 5em;
    height: 5em;
    position: absolute;
    top: -10px;
    right: -10px;
    border-top: 2.5px solid #4AC6B2;
    border-right: 2.5px solid #4AC6B2;
  }

  &::before {
    content: "";
    width: 5em;
    height: 5em;
    position: absolute;
    bottom: -10px;
    left: -10px;
    border-bottom: 2.5px solid #4AC6B2;
    border-left: 2.5px solid #4AC6B2;
  }
}
.me_description{
  padding: 1em;
  font-size: 1.2em;
  text-align: center;
}
.about_me_welcome{
  span{
    font-weight: bolder;
  }
}
.second_line_welcome{
  margin-top: 7px;
}
.third_line_welcome {
  margin-top: 5px;
}

.about_picture {
  width: 185.85px;
  height: 268.8px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;


  &::before {
    content: "";
    width: 5em;
    height: 5em;
    position: absolute;
    bottom: 0;
    left: 0;
    border-bottom: 2.5px solid #4AC6B2;
    border-left: 2.5px solid #4AC6B2;
  }

  &::after {
    content: "";
    width: 5em;
    height: 5em;
    position: absolute;
    top: 0;
    right: 0;
    border-top: 2.5px solid #4AC6B2;
    border-right: 2.5px solid #4AC6B2;
  }
}

.me_img {
  width: 90%;
  height: 90%;
  border-radius: 2px;
  -webkit-box-shadow: 6px 10px 13px 3px rgba(214, 210, 214, 1);
  -moz-box-shadow: 6px 10px 13px 3px rgba(214, 210, 214, 1);
  box-shadow: 6px 10px 13px 3px rgba(214, 210, 214, 1);
}


